.font-type{
  font-family: 'Ubuntu', sans-serif;
}
.red{
  color:#ec4670;
}

.fade-transition {
  transition: all .7s ease;
  opacity: 1;
}

.fade-enter, .fade-leave {
  opacity: 0;
}
.content{
  margin: 1% 0;
  margin-left: 3%;
  width: 90%;
  height: 70%;
  padding: 1% 2%;
  display: block;
  float: left;
  overflow: hidden;
  position: relative;
  background-color: #eee;
  animation:slide-run 1s 1;
}
.title{
  font-family: 'Ubuntu', sans-serif;
  font-weight: lighter;
}
.txt-left{
  text-align: left;
}

/* input */
input[type=text]{
  color: #42b983;
  font-size: 16px;
  border: 1px solid #fff;
  border-bottom: 1px solid #ccc;
}
textarea{
  margin:5px 0;
  width: 100%;
  border: none;
  color: #42b983;
  font-size: 16px;
  background-color: #eee;
}

.confirm{
  height: 40px;
  display: block;
  border: none;
  font-size: 16px;
  font-weight: normal;
  color:#fff;
  transition: all 0.2s ease;
  background-color: #67d1d5;
  box-shadow:1px 3px 3px #bbb;
  cursor: pointer;
}
.confirm.btn-error{
  background-color: #fff;
  color: #67d1d5;
  box-shadow:none;
}
.confirm:hover{
    background-color: #41abaf;
  }
.confirm.btn-error:hover{
  background-color: #eee;
}
.close{
  width: 40px;
  height: 40px;
  display: block;
  position:absolute;
  top: 5px;
  right: 5px;
  z-index: 999;
  cursor: pointer;
 }
 .close span{
    width: 20px;
    height: 1px;
    display: block;
    position:absolute;
    background-color:#777;
  }
.close span:first-child{
    top: 20px;
    left: 10px;
    transform: rotateZ(45deg);
  }
.close span:nth-child(2){
    top: 20px;
    left: 10px;
    transform: rotateZ(135deg);
  }
 .btn-shop, .btn-cancel{
  padding:8px 15px;
  border:none;
  text-align: center;
  background-color:#67d1d5;
  color: #fff;
  border-radius:5px;
  cursor: pointer;
  
 }
 .btn-cancel{
  background-color: #888;
 }
  .btn-cancel:hover{
  background-color: #555;
 }
 .btn-shop:hover{
    background-color:#37979a;
  }

/* seperate block */
.line{
  margin:15px auto;
  width: 30px;
  height: 1px;
  display: block;
  background-color:#67d1d5;
}

@keyframes slide-run {
  0% {
    height: 0;
  }
  100% {
    height: 70%;
  }
}


